import React from 'react';
import './App.css';

import Home from './Content/Home.js';
import Bubble from './Content/Bubble.js'
import Insertion from './Content/Insertion.js'
import Selection from './Content/Selection.js'
import Merge from './Content/InPlaceMerge.js'
import Quick from './Content/Quick.js'
import Heap from './Content/Heap.js'

import {Route} from 'react-router-dom'

function App() {
  return (
    <div className="App">
      <Route exact path="/algoviz" component={Home} />
      <Route exact path="/algoviz/bubble-sort" component={Bubble} />
      <Route exact path="/algoviz/insertion-sort" component={Insertion} />
      <Route exact path="/algoviz/selection-sort" component={Selection} />
      <Route exact path="/algoviz/inplace-merge-sort" component={Merge} />
      <Route exact path="/algoviz/quick-sort" component={Quick} />
      <Route exact path="/algoviz/heap-sort" component={Heap} />
    </div>
  );
}

export default App;
